(function() {
  function InitLoading(opts) {
    var self = this;
    var defaultOpts = {
      element: document.body,
      circle: {
        radius: {
          min: '10%',
          step: '5%'
        },
        numOfCircle: 6,
        animationName: 'loadingRot',
        animationTimingFunction: 'linear',
        animationDelay: '',
        animationIterationCount: 'infinite',
        animationDirection: 'alternate',
        animationFillMode: '',
        animationPlayState: 'initial',
        animationDuration: {
          min: 1500,
          step: 500
        },
      }
    };

    function _parseOpts(opts, defaultOpts) {
      var _opts = {};
      var tmpOptsArr;

      if (typeof defaultOpts === 'object') {
        tmpOptsArr = Object.keys(defaultOpts);
        tmpOptsArr.forEach(function(optName, idx) {
          if (optName === 'element') {
            if (opts === undefined || opts[optName] === undefined) {
              _opts[optName] = defaultOpts[optName];
            } else {
              _opts[optName] = opts[optName];
            }
            return;
          }

          // handle object options
          if (typeof defaultOpts[optName] === 'object') {
            _opts[optName] = _parseOpts(typeof opts === 'object' ? opts[optName] : undefined, defaultOpts[optName]);
            // handle primitive options
          } else {
            if (opts === undefined || opts[optName] === undefined) {
              _opts[optName] = defaultOpts[optName];
            } else {
              _opts[optName] = opts[optName];
            }
          }
        });
      }
      return _opts;
    }

    function _genValueArr(opts) {
      var outArr = [];
      var pattern = /^(\d+)(.*)$/;
      var min = opts.min;
      var step = opts.step;
      var num = opts.num;
      var unit = '';
      var stepUnit = '';
      var sameUnitFlag = true;
      var parsedMin = min.match && min.match(pattern);
      var parsedStep = min.match && step.match(pattern);

      if (!parsedMin || !(parsedMin instanceof Array) || parsedMin[1] === '') {
        min = typeof min === 'number' ? min : 0;
      } else {
        min = parseFloat(parsedMin[1]);
        unit = parsedMin[2]; // only take the unit of min value
      }

      if (!parsedStep || !(parsedStep instanceof Array) || parsedStep[1] === '') {
        step = typeof step === 'number' ? step : 0;
      } else {
        step = parseFloat(parsedStep[1]);
        stepUnit = parsedStep[2]; // used for comparision
      }

      sameUnitFlag = unit === stepUnit;

      for (var i = 0; i < num; i++) {
        if (sameUnitFlag) {
          outArr.push((min + i * step) + unit);
        } else {
          if (unit) {
            outArr.push((min * Math.pow(step, i)) + unit);
          } else {
            outArr.push(min + i * step);
          }
        }
      }

      return outArr;
    }

    function _render(opts) {
      var svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
      svg.className.baseVal += ' jl-loading';
      svg.setAttributeNS("http://www.w3.org/2000/xmlns/", "xmlns:xlink", "http://www.w3.org/1999/xlink");
      var circle;
      var style;
      var animation;

      for (var i = 0; i < opts.circle.numOfCircle; i++) {
        animation = opts.circle.animationName + ' ' + opts.genAnimationDuration[opts.circle.numOfCircle - i - 1] + 'ms ' + opts.circle.animationTimingFunction + ' ' + opts.circle.animationDelay + ' ' + opts.circle.animationIterationCount + ' ' + opts.circle.animationDirection + ' ' + opts.circle.animationFillMode;

        // animationPlayState: 'initial',

        style = 'stroke-dasharray:' + opts.genRadius[opts.circle.numOfCircle - i - 1] + ';-webkit-animation:' + animation + ';animation:' + animation + ';';

        circle = document.createElementNS("http://www.w3.org/2000/svg", "circle");
        circle.setAttribute('id', 'c' + (i + 1));
        circle.setAttribute('cx', '50%');
        circle.setAttribute('cy', '50%');
        circle.setAttribute('r', '' + opts.genRadius[opts.circle.numOfCircle - i - 1]);
        circle.setAttribute('style', style);
        svg.appendChild(circle);
      }
      opts.element.appendChild(svg);
    }

    function _updateTransformOrigin() {
      var wrapper = self._opts.element;
      var circles = wrapper.querySelectorAll('circle');
      var transformOrigin = {
        x: wrapper.clientWidth / 2,
        y: wrapper.clientHeight / 2
      };


      for (var i = 0; i < circles.length; i++) {
        circles[i].style.transformOrigin = transformOrigin.x + 'px ' + transformOrigin.y + 'px';
      }
    }

    self._opts = _parseOpts(opts, defaultOpts);
    self._opts.genAnimationDuration = _genValueArr({
      min: self._opts.circle.animationDuration.min,
      step: self._opts.circle.animationDuration.step,
      num: self._opts.circle.numOfCircle,
    });
    self._opts.genRadius = _genValueArr({
      min: self._opts.circle.radius.min,
      step: self._opts.circle.radius.step,
      num: self._opts.circle.numOfCircle,
    });

    _render(self._opts);
    _updateTransformOrigin();
    window.addEventListener('resize', function() {
      _updateTransformOrigin();
    });
  }

  // export
  window.jing = window.jing || {};
  window.jing.InitLoading = InitLoading;
}());
